import {
  Component, HostListener, OnDestroy, OnInit
} from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { Router, RouterModule } from '@angular/router';
import { UserSuspiciousActivity } from '@biz-nest/auth-client';
import { expenseCategory } from '@biz-nest/core-client';
import {
  AppNameService,
  EventbusService,
  ListComposeAdminPanel,
  MainBarTitleService,
  scrollToTop,
  TooltipService, WindowService
} from '@front-shared-lib';
import { ToastrService } from 'ngx-toastr';
import { AdminDateFilterFieldComponent } from '../../misc/admin-date-filter-field/admin-date-filter-field.component';
import { AdminModalComponent } from '../../misc/admin-modal/admin-modal.component';
import { AdminPageContentComponent } from '../../misc/admin-page-content/admin-page-content.component';
import { AdminSearchFieldComponent } from '../../misc/admin-search-field/admin-search-field.component';
import { AdminTableComponent } from '../../misc/admin-table/admin-table.component';
import { AdminTopBarComponent } from '../../misc/admin-top-bar/admin-top-bar.component';

@Component({
  selector: 'app-user-suspicious-activity',
  standalone: true,
  imports: [
    RouterModule,
    AdminModalComponent,
    AdminTableComponent,
    AdminSearchFieldComponent,
    AdminDateFilterFieldComponent,
    AdminTopBarComponent,
    AdminPageContentComponent
  ],
  templateUrl: './user-suspicious-activity.component.html'
})
export class UserSuspiciousAuditComponent
implements OnInit, OnDestroy {
  expenseCategory = expenseCategory;
  cleanup?: () => void;
  catFilter?: string;

  listCompose = new ListComposeAdminPanel<UserSuspiciousActivity>(
    UserSuspiciousActivity,
    this.eventbus,
    this.windowService,
    this.toastr
  );

  actions = [
    {
      label: 'View User',
      icon: 'visibility',
      event: (userSuspiciousActivity: UserSuspiciousActivity) => {
        this.viewUser(userSuspiciousActivity.user._id);
      }
    },
    {
      label: 'Delete',
      icon: 'delete',
      event: (userSuspiciousActivity: UserSuspiciousActivity) => {
        this.listCompose.openDeleteOneModal(
          userSuspiciousActivity._id,
          'Delete User Suspicious Activity',
          'Are You Sure You Want To Delete This User Suspicious Activity?'
        );
      }
    }
  ];


  constructor(
    public eventbus: EventbusService,
    private router: Router,
    private windowService: WindowService,
    private toastr: ToastrService,
    public tooltipService: TooltipService,
    private apNme: AppNameService,
    public mainBarTitle: MainBarTitleService,
    private metaTagService: Meta,
    private titleService: Title
  ) {}

  @HostListener('document:click', ['$event'])
  onDocumentClick() {
    this.listCompose.currentActivePopupId = '';
  }

  ngOnInit(): void {
    this.listCompose.getDataArr();
    this.mainBarTitle.title = 'User Suspicious Activity - Manage User Suspicious Activity';
    this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
    this.metaTagService.updateTag({
      name: 'description',
      content: `Easily manage and view user IP addresses for your organization
      to ensure effective tracking and security.`
    });

    scrollToTop(this.windowService);
  }

  viewUser(id: string) {
    this.router.navigate(['/manage-user/' + id]);
  }

  ngOnDestroy() {
    this.cleanup?.();
    this.listCompose.cleanup?.();
  }
}

